body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, Arial, Helvetica,
    sans-serif;
}

/* Only chrome, firefox is bugged */
.selector:not(*:root), html{
  scroll-behavior: smooth;
}
.header {
  height: 90px;
  width: 100%;
  background: #1e2028;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

@media screen and (max-width: 1183px) {
  .header {
    height: 140px;
  }
}

@media screen and (max-width: 865px) {
  .header {
    height: 190px;
  }
}


.header-logo-wrapper {
  display: flex;
  border: none;
  margin: 0;
  padding: 0;
  background: transparent;
  font: inherit;
  outline: none;
  cursor: pointer;
  height: 50px;
  margin-top: 16px;
  width: 25%;
}

@media screen and (max-width: 1183px) {
  .header-logo-wrapper {
    margin: 0 auto;
    margin-top: 13px;
    width: 300px;
  }
}

.header-text {
  font-size: 36px;
  color: #f4f4f4;
  opacity: 0.93;
  margin-top: 6px;
}

.header-text:hover {
  opacity: 1;
}

.header-logo {
  -webkit-animation: App-logo-spin infinite 10s linear;
          animation: App-logo-spin infinite 10s linear;
  height: 50px;
  pointer-events: none;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* overriding component (React-Select) css styles, React-Select library 
wasn't optimized for using plain css so I make this workaround  */

.react-select-container {
  height: 39px !important;
}

.react-select__menu {
  cursor: pointer !important;
  color: #f4f4f4 !important;
  opacity: 0.95 !important;
  border: 1px solid #1e2028 !important;
  background-color: #23252b !important;
  -webkit-transition: 0.3s !important;
  transition: 0.3s !important;
}

.react-select__control {
  height: 39px !important;
  background-color: #1e2028 !important;
  border: 1px solid #424754 !important;
  cursor: pointer !important;
  outline: none !important;
  box-shadow: none !important;
  min-width: 200px;
  display: flex !important;
}

.react-select__input {
  color: #f4f4f4 !important;
  opacity: 0.7 !important;
}

.react-select__option:hover {
  background-color: #424754 !important;
  cursor: pointer !important;
}

.react-select__option {
  background-color: #23252b !important;
}

.react-select__option--is-selected {
  background-color: #424754 !important;
}

.react-select__single-value {
  color: #8e95a5 !important;
  font-size: 17px;
}

.css-19bqh2r {
  fill: #8e95a5 !important;
  opacity: 0.8 !important;
}
.css-19bqh2r:hover {
  fill: #9ca3b3 !important;
  opacity: 1 !important;
}

.react-select__indicator-separator {
  background-color: #8e95a5 !important;
}

/* scroll */
.react-select__menu-list::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  background-color: #2d3038;
}

.react-select__menu-list::-webkit-scrollbar {
  width: 8px;
  background-color: #23252b;
}

.react-select__menu-list::-webkit-scrollbar-thumb {
  border-radius: 5px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #1e2028;
}

.filters-container {
  display: flex;
  width: 75%;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 25px;
  min-width: 875px;
  justify-content: flex-end;
}

@media screen and (max-width: 1182px) {
  .filters-container {
    margin: 0 auto;
  }
}

@media screen and (max-width: 864px) {
  .filters-container {
    min-width: 0;
    margin-top: 10px;
    height: 100px;
    justify-content: normal;
  }
}

@media screen and (max-width: 675px) {
  .filters-container {
    width: 95%;
  }
}

.filter-buttons-container {
  max-width: 65%;
  display: flex;
  margin-right: 25px;
}

@media screen and (max-width: 1182px) {
  .filter-buttons-container {
    margin-left: 5%;
  }
}
@media screen and (max-width: 870px) {
  .filter-buttons-container {
    width: 405px;
    margin: 0 auto;
  }
}
@media screen and (max-width: 675px) {
  .filter-buttons-container {
    margin: 0 0;
  }
}

@media screen and (max-width: 570px) {
  .filter-buttons-container {
    max-width: none;
  }
}

.search-wrapper {
  position: relative;
  display: inline-block;
  width: 29%;
  margin-right: 50px;
}

@media screen and (max-width: 864px) {
  .search-wrapper {
    height: 39px;
    margin-bottom: 28px;
  }
}
@media screen and (max-width: 435px) {
  .search-wrapper {
    width: 80%;
  }
}

.search-form {
  position: relative;
}

.search-input {
  outline: none;
  -webkit-transition: all 0.2s ease 0s;
  transition: all 0.2s ease 0s;
  padding: 0 10px;
  height: 37px;
  border: 1px solid #424754;
  background: 0 0;
  color: #8e95a5;
  line-height: 32px;
  border-radius: 3px;
  cursor: text;
  font-size: 17px;
  text-align: start;
  min-width: 225px;
  width: 100%;
}

@media screen and (max-width: 864px) {
  .search-input {
    width: 383px;
    margin-top: 15px;
    margin-left: 61%;
  }
}

@media screen and (max-width: 675px) {
  .search-input {
    margin-left: 0px;
    margin-top: 10px;
  }
}

@media screen and (max-width: 435px) {
  .search-input {
    width: 100%;
  }
}

.search-input::-webkit-input-placeholder {
  color: #8e95a5;
  opacity: 0.8 !important;
}

.search-input::-moz-placeholder {
  color: #8e95a5;
  opacity: 0.8 !important;
}

.search-input:-ms-input-placeholder {
  color: #8e95a5;
  opacity: 0.8 !important;
}

.search-input::-ms-input-placeholder {
  color: #8e95a5;
  opacity: 0.8 !important;
}

.search-input::placeholder {
  color: #8e95a5;
  opacity: 0.8 !important;
}

.search-button {
  position: absolute;
  top: 4px;
  right: -17px;
  display: block;
  width: 32px;
  height: 32px;
  border: none;
  background: 0 0;
  color: #646b7f;
  font-size: 1.2rem;
  padding: 0;
}

@media screen and (max-width: 864px) {
  .search-button {
    display: none;
  }
}

.filter-buttons {
  display: block;
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  color: inherit;
  font: inherit;
  line-height: normal;
  outline: none;
  text-align: inherit;
  height: 39px;
  margin-left: 15px;
}

@media screen and (max-width: 570px) {
  .filter-buttons {
    margin-left: 10px;
  }
}

.ratting-button {
  -webkit-transition: all 0.2s ease 0s;
  transition: all 0.2s ease 0s;
  padding: 0 10px;
  border: 1px solid #424754;
  background: 0 0;
  color: #8e95a5;
  line-height: 32px;
  border-radius: 3px;
  cursor: pointer;
  font-size: 17px;
  text-align: start;
  min-width: 76px;
}

.ratting-button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.popularity-button {
  -webkit-transition: all 0.2s ease 0s;
  transition: all 0.2s ease 0s;
  padding: 0 10px;
  border: 1px solid #424754;
  background: 0 0;
  color: #8e95a5;
  line-height: 32px;
  border-radius: 3px;
  cursor: pointer;
  font-size: 17px;
  text-align: start;
  min-width: 98px;
}

@media screen and (max-width: 425px) {
  .popularity-button {
    display: none;
  }
}

.popularity-button:-moz-focus-inner {
  border: 0;
  padding: 0;
}

.movie-wrapper {
  display: flex;
  list-style-type: none;
  width: 47%;
  height: 200px;
  padding: 15px;
}
@media screen and (max-width: 1045px) {
  .movie-wrapper {
    width: 70%;
    margin: 0 auto;
  }
}

@media screen and (max-width: 679px) {
  .movie-wrapper {
    width: 90%;
  }
}

.movie-wrapper:hover {
  background: #30333f;
  border-radius: 7px;
  -webkit-transition: 200ms ease-in-out;
  transition: 200ms ease-in-out;
}
.movie-wrapper:hover > .movie-poster {
  -webkit-transition: 200ms ease-in-out;
  transition: 200ms ease-in-out;
  opacity: 1;
}
.movie-wrapper:hover > .movie-info {
  -webkit-transition: 200ms ease-in-out;
  transition: 200ms ease-in-out;
  opacity: 1;
}

.movie-poster {
  border-radius: 7px;
  position: relative;
  display: block;
  height: 200px;
  width: 150px;
  opacity: 0.9;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.movie-info {
  margin-left: 15px;
  overflow-y: auto;
  overflow-x: hidden;
  height: 200px;
  color: #f4f4f4;
  opacity: 0.9;
  padding-right: 10px;
  cursor: default;
}

.movie-info::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #2d3038;
}

.movie-info::-webkit-scrollbar {
  width: 8px;
  background-color: #23252b;
  border-radius: 10px;
}

.movie-info::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #1e2028;
}

.movie-title {
  font-size: 20px;
  color: #f4f4f4;
  word-wrap: break-word;
  margin-bottom: 12px;
  display: inline-block;
  margin-right: 14px;
}

.movie-rank {
  position: relative;
  display: inline-block;
  margin-bottom: 13px;
}

.movie-rank > :first-child{
  color: #fbb829;
  padding-right: 10px;
}

.movie-rank-number {
  color: #f4f4f4;
  padding-left: 13px;
}

.fa-star {
  position: absolute;
  top: 4px;
  left: 47px;
  font-size: 10px;
  display: inline-block;
  color: #fbb829;
}




.main-wrapper {
  height: 100%;
  background: #23252b;
  padding-top: 14px;
  padding-bottom: 20px;
  /* useful during reloading */
  min-height: 1300px;
}

.movies-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  height: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

.loading {
  margin-top: 25px;
  text-align: center;
  font-size: 30px;
  color: #646b7f;
}

.pagination-wrapper {
  height: 125px;
  background: #1e2028;
  display: flex;
}

.pagination {
  margin: 0 auto;
  padding-top: 45px;
  display: flex;
  padding-left: 0;
  border-radius: 4px;
}

.pagination > li {
  display: inline;
}

.pagination > li > a,
.pagination > li > span {
  font-size: 17.5px;
  position: relative;
  float: left;
  padding: 6px 12px;
  line-height: 1.42857143;
  text-decoration: none;
  color: #f4f4f4;
  opacity: 0.9;
  background: #30333f;
  border: 1px solid #1e2028;
  margin-left: -1px;
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer;
}

.pagination > li > span:hover {
  opacity: 1;
}

.pagination > li:first-child > a,
.pagination > li:first-child > span {
  margin-left: 0;
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
}
.pagination > li:last-child > a,
.pagination > li:last-child > span {
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
}
.pagination > li > a:hover,
.pagination > li > span:hover,
.pagination > li > a:focus,
.pagination > li > span:focus {
  z-index: 2;
  color: #1e2028;
  opacity: 0.9;
  background-color: #f4f4f4;
  border-color: #30333f;
  outline: none;
}
.pagination > .active > a,
.pagination > .active > span,
.pagination > .active > a:hover,
.pagination > .active > span:hover,
.pagination > .active > a:focus,
.pagination > .active > span:focus {
  z-index: 3;
  color: #fff;
  background-color: #575b6d;
  cursor: default;
}
.pagination > .disabled > span,
.pagination > .disabled > span:hover,
.pagination > .disabled > span:focus,
.pagination > .disabled > a,
.pagination > .disabled > a:hover,
.pagination > .disabled > a:focus {
  color: #f4f4f4;
  opacity: 0.9;
  background-color: #30333f;
  border-color: #1e2028;
  cursor: not-allowed;
}
.pagination-lg > li > a,
.pagination-lg > li > span {
  padding: 10px 16px;
  font-size: 25px;
  line-height: 1.3333333;
}
.pagination-lg > li:first-child > a,
.pagination-lg > li:first-child > span {
  border-bottom-left-radius: 6px;
  border-top-left-radius: 6px;
}
.pagination-lg > li:last-child > a,
.pagination-lg > li:last-child > span {
  border-bottom-right-radius: 6px;
  border-top-right-radius: 6px;
}
.pagination-sm > li > a,
.pagination-sm > li > span {
  padding: 5px 10px;
  font-size: 20px;
  line-height: 1.5;
}
.pagination-sm > li:first-child > a,
.pagination-sm > li:first-child > span {
  border-bottom-left-radius: 3px;
  border-top-left-radius: 3px;
}
.pagination-sm > li:last-child > a,
.pagination-sm > li:last-child > span {
  border-bottom-right-radius: 3px;
  border-top-right-radius: 3px;
}

